import * as React from "react";
import Header from "./header";
import { appContext } from "../hooks/provider";
import LoginView from "./login";
import Footer from "./footer";
import { SEO } from "./seo";
import { CookieBanner, UpdatesBanner } from "./atoms";
import { v4 as uuidv4 } from "uuid";
import { getBrowserFingerprint } from "./utils";

type Props = {
  title: string;
  link: string;
  children?: React.ReactNode;
  showHeader?: boolean;
  restricted?: boolean;
  meta?: any;
};

const Layout = ({
  meta,
  title,
  link,
  children,
  showHeader = true,
  restricted = false,
}: Props) => {
  React.useEffect(() => {
    const gtagID = process.env.GATSBY_GTAG_ID;
    const gaClientId = getBrowserFingerprint();
    // function gtag(){dataLayer.push(arguments);}

    if (gtagID && typeof window !== "undefined") {
      const script = document.createElement("script");
      script.src = `https://www.googletagmanager.com/gtag/js?id=${gtagID}`;
      script.async = true;
      document.body.appendChild(script);
      const script2 = document.createElement("script");
      script2.innerHTML = `
      window.dataLayer = window.dataLayer || [];
      
      function gtag(){dataLayer.push(arguments);}
      // console.log("dataLayer", window.dataLayer)
      gtag('consent', 'default', {
        'ad_storage': 'denied',
        'analytics_storage': 'denied'
      });
      
      gtag('config', '${gtagID}', { 'client_storage': 'none', 'anonymize_ip': false, client_id: '${gaClientId}', send_page_view: true, 'storage': 'none' });  
      gtag('js', new Date());
      `;
      document.body.appendChild(script2);
    }
  }, []);

  const LayoutView = ({ darkMode }: { darkMode: string }) => {
    React.useEffect(() => {
      document.getElementsByTagName("html")[0].className = `${
        darkMode === "dark" ? "dark bg-primary" : "light bg-primary"
      } `;
    }, [darkMode]);

    console.log(meta);

    return (
      <div className={` flex flex-col`}>
        <div className="flex-1 text-primary">
          {/* <title>{meta?.title + " | " + title}</title> */}
          <SEO
            title={meta?.title + " | " + title}
            description={meta?.description}
          />
          {showHeader && <Header meta={meta} link={link} />}

          {/* <CookieBanner /> */}
          <div>{children}</div>
        </div>
        <Footer />
      </div>
    );
  };
  return (
    <appContext.Consumer>
      {(context: any) => {
        if (restricted) {
          return (
            <div className="h-screen bg-yellow-100 ">
              {context.user && <LayoutView darkMode={context.darkMode} />}
              {!context.user && <LoginView meta={meta} />}
            </div>
          );
        } else {
          return <LayoutView darkMode={context.darkMode} />;
        }
      }}
    </appContext.Consumer>
  );
};

export default Layout;
